.page {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  // background-color: $accent;
  //   clip-path: circle(10% at 10% 10%);

}

.page--decor {
  &::before,
  &::after {
    position: absolute;

  }

  &::before {
    content: '';
    width: 100%;
    height:  100%;
    background-color: $accent;
    clip-path: circle(57.7vw at 3.02vw -5.51vw);
  }

  &::after {
    display: flex;
    align-items: center;
    padding-left: 55px;
    top: 4.16vw;
    left: 4.16vw;
    content: 'Free files';
    color: $white;
    font-weight: 700;
    font-size: 1.6vw;
    font-family: $text-font;
    height: 50px;
    background-image: url(../img/Logo/Figma.png);
    background-size: 50px 50px;
    background-position: 0 0;
    background-repeat: no-repeat;
    @media only screen and (-webkit-min-device-pixel-ratio: 2){
      padding-left: 110px;
      background-image: url(../img/Logo/Figma@2x.png);
      background-size: 100px 100px;
      height: 100px;
    }
  }
}
.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  padding:  10.77vw 0;
}

.wrapper--decor {
  &::before,
  &::after {
    content: '';
    position: absolute;
  }

  &::before {
    right: 9vw;
    top: 3.35vw;
    width: 1.14vw ;
    height:  1.14vw ;
    background-color: $accent;
    border-radius: 50%;
  }

  &::after {
    right: 2.43vw;
    top:  18.73vw;
    width: 3.95vw ;
    height:  3.95vw ;
    background-color: $accent;
    border-radius: 50%;
    background-color: #505155;
  }
}

.container {
  max-width: 89.93vw;
  margin: 0 auto;
  padding-left:  3.67vw;
  padding-right:  3.67vw;
}