.phoenix{
  &--decor {
    &::before,
    &::after {
        content: '';
        top: -15px;
        position: absolute;
        width: 4px;
        height: 4px;
        background-color: $decor;
        @media only screen and (-webkit-min-device-pixel-ratio: 2) {
        width: 8px;
        height: 8px;
        }
    }

    &::before {
      left: -47px;
    }

    &::after {
      left: -59px;
    }
  }

  &__inner--decor {
    &::before,
    &::after {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        background-color: $decor;
        @media only screen and (-webkit-min-device-pixel-ratio: 2) {
        width: 40px;
        height: 40px;
        }
    }

    &::before {
      right: 36px;
      top: -11px;
    }

    &::after {
      right: -35px;
      bottom: 10px;
    }
  }
}

