.footer--decor {
  &::before,
  &::after {
    content: '';
    position: absolute;
    border-radius: 50%;
  }

  &::before {
    bottom: -130px;
    left: 90px;
    width: 32px;
    height: 32px;

    background-color: #505155;
  }
  &::after {
    bottom: -370px;
    left: 150px;
    width: 230px;
    height: 230px;
    background-color: #efefef;
  }
}

.container--decor {
  &::before,
  &::after {
    content: '';
    position: absolute;
    border-radius: 50%;
  }

  &::before {
    bottom: -500px;
    right: -150px;
    width: 700px;
    height: 700px;
    background-color: #292C31;
    z-index: -1;
  }
}